export class Problem {
    constructor(type, question, answer, input_answer_hint = "") {
        this.type = type;
        this.question = question;
        this.answer = answer;
        this.input_answer_hint = input_answer_hint;
        this.result = "?";
        this.tries = 0;
        this.time = 0;
        this.hints = 0;
        /* type: "input",
            question: `<h2>Convert ${subscript(binary, 2)} to hex?</h2>`,
            answer: "0x" + hex,
            input_answer_hint: "prefix with '0x', example: 0xbfa11", */
    }
}
export class ProblemSet {
    constructor(title, id, tags, gen, problems = [], resources = [], emoji_mark = "❓") {
        this.title = title;
        this.id = id;
        this.problem_index = 0;
        this.problems = problems;
        this.tags = tags;
        this.gen = gen;
        this.resources = resources;
        this.emoji_mark = emoji_mark;
        this.last_updated = 0;
    }
}
export function has_started(problems) {
    for (let i = 0; i < problems.length; i++) {
        if (problems[i].tries >= 1) {
            return true;
        }
    }
    return false;
}
//adds back the gen() functions that were not seralized
export function merge_gen_funcs(without, original) {
    return without.map((val) => {
        let found = original.find((hay) => val.id === hay.id);
        if (found) {
            val.gen = found.gen;
            return val;
        }
        else {
            return val;
        }
    });
}
export function gen_amount(amount = 1, f) {
    return function g() {
        let res = [];
        for (let i = 0; i < amount; i++) {
            res.push(f());
        }
        console.log(res);
        return res;
    };
}
